import React, { createElement } from 'react'
import { Row, Link, Button, Label, Head, SubHead, P } from '../components'
import { Grid, Col } from 'react-bootstrap'
import rehypeReact from 'rehype-react'

const renderAst = new rehypeReact({
  createElement,
  components: {
    p: P,
    h3: SubHead
  }
}).Compiler

export default ({ data }) => {
  const { markdownRemark: { htmlAst, frontmatter } } = data
  return (
      <Grid>
        <Row>
          <Col md={12}>
            { frontmatter.subtitle && <Label>{ frontmatter.subtitle }</Label> }
            { frontmatter.title && <Head>{ frontmatter.title }</Head> }
            <div>{renderAst(htmlAst)}</div>
          </Col>
        </Row>
      </Grid>
  )
}
export const query = graphql`
  query IndedxQuery {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(frontmatter: {slug: {eq: "index"}}) {
      excerpt
      htmlAst
      frontmatter {
        title
        subtitle
      }
    }
  }
`



